//用于创建整个应用的路由器
import VueRouter from "vue-router";
import Home from "@/pages/Home";
//引入页面

//捕捉异常
const originalPush = VueRouter.prototype.push

VueRouter.prototype.push = function push(location) {
    return originalPush.call(this, location).catch(err => err)
}


//创建并暴露路由器
const router = new VueRouter({
    // mode: 'history',
    routes: [
        //主页路由
        {
            name: 'home',
            path: '/',
            component: Home,
            meta: {
                index: 0
            }
        },
        //高校风采
        {
            name: 'university',
            path: '/university',
            component: () => import("@/pages/University"),
            meta: {
                index: 1
            },
            children: [
                //文章列表
                {
                    name: 'passageListUniversity',
                    path: '',
                    component: () => import("@/pages/passageList/PassageListUniversity"),
                    meta: {
                        index: 2,
                    }
                },
                //浙江工业大学
                {
                    name: 'zjut',
                    path: 'zjut',
                    component: () => import("@/pages/university/Zjut"),
                    meta: {
                        index: 3
                    }
                }
            ]
        },
        //乡镇风采
        {
            name: 'township',
            path: '/township',
            component: () => import("@/pages/TownShip"),
            meta: {
                index: 1
            },
            children: [
                //文章列表
                {
                    name: 'passageListTownShip',
                    path: '',
                    component: () => import("@/pages/passageList/PassageListTownShip"),
                    meta: {
                        index: 2,
                    }
                },
                //江西省上饶市鄱阳县双港乡
                {
                    name: 'tsg',
                    path: 'tsg',
                    component: () => import("@/pages/township/Tsg"),
                    meta: {
                        index: 3
                    }
                },
                //饶市鄱阳县莲湖乡
                {
                    name: 'tlh',
                    path: 'tlh',
                    component: () => import("@/pages/township/Tlh"),
                    meta: {
                        index: 3
                    }
                },
                //浙江省丽水市青田县鹤城
                {
                    name: 'thc',
                    path: 'thc',
                    component: () => import("@/pages/township/Thc"),
                    meta: {
                        index: 3
                    }
                }
            ]

        },
        //团队资讯
        {
            name: 'team',
            path: '/team',
            component: () => import("@/pages/Team"),
            meta: {
                index: 1
            },
            children: [
                //文章列表
                {
                    name: 'passageListTeam',
                    path: '',
                    component: () => import("@/pages/passageList/PassageListTeam"),
                    meta: {
                        index: 2,
                    }
                },
                //下姜村
                {
                    name: 'xjc',
                    path: 'xjc',
                    component: () => import("@/pages/team/Xjc"),
                    meta: {
                        index: 3
                    }
                }

            ]
        },
        //律所风采
        {
            name: 'lawFirm',
            path: '/lawFirm',
            component: () => import("@/pages/LawFirm"),
            meta: {
                index: 1
            },
            children: [
                //文章列表
                {
                    name: 'passageListLawFirm',
                    path: '',
                    component: () => import("@/pages/passageList/PassageListLawFirm"),
                    meta: {
                        index: 2,
                    }
                },
                //上海靖之霖（杭州）律师事务所
                {
                    name: 'jzl',
                    path: 'jzl',
                    component: () => import("@/pages/lawFirm/Jzl"),
                    meta: {
                        index: 3
                    }
                },
                //浙江泽鼎律师事务所
                {
                    name: 'zd',
                    path: 'zd',
                    component: () => import("@/pages/lawFirm/Zd"),
                    meta: {
                        index: 3
                    }
                }
            ]
        },
        //法律工具
        {
            name: 'legalTools',
            path: '/legalTools',
            component: () => import("@/pages/LegalTools"),
            meta: {
                index: 1
            },
            children: [
                //常见问题
                {
                    name: 'faq',
                    path: 'faq',
                    component: () => import("@/pages/legalTools/Faq"),
                    meta: {
                        index: 2
                    }
                },
                //文书模板
                {
                    name: 'instrumentTemplate',
                    path: 'instrumentTemplate',
                    component: () => import("@/pages/legalTools/InstrumentTemplate"),
                    meta: {
                        index: 2
                    }
                },
                //赔偿计算
                {
                    name: 'compensationCalculation',
                    path: 'compensationCalculation',
                    component: () => import("@/pages/legalTools/CompensationCalculation"),
                    meta: {
                        index: 2
                    }
                }
            ]
        }
    ],
})


export default router